import React from "react";
import { List } from "./list";
import { Project } from "../../types/project";
import { SearchPanel } from "./search-panel";
import { useState, useEffect } from "react";
import {
  cleanObject,
  useDebounce,
  useDocumentTitle,
  useMount,
} from "../../utils";
import * as qs from "qs";
import { useHttp } from "utils/http";
import styled from "@emotion/styled";
import { Button, Row, Typography } from "antd";
import { useAsync } from "../../utils/use-async";
import { useProjects } from "../../utils/project";
import { useUsers } from "../../utils/user";
import { Helmet } from "react-helmet";
import { Test } from "../../components/test-closure";
import { useProjectModal, useProjectsSearchParams } from "./util";
import { ButtonNoPadding, ErrrorBox } from "components/lib";

//基本类型，可以放到依赖里。组件状态，可以放到依赖里。非组件状态的对象，绝不可以放到依赖里
//https://codesandbox.io/s/boring-hoover-jxvq2?file=/src/App.js

//使用js的同学，大部分的错误都是在runtime(运行时)的时候发现的
//我们希望，在静态代码中，就能找到其中的一些错误 -> 强类型
export const ProjectListScreen = () => {
  useDocumentTitle("项目列表", false);

  const { open } = useProjectModal();

  const [param, setParam] = useProjectsSearchParams();
  const { isLoading, error, data: list } = useProjects(useDebounce(param, 200));
  const { data: users } = useUsers();
  // console.log(retry,'retry')
  return (
    <Container>
      <Row justify={"space-between"}>
        <h1>项目列表</h1>
        <ButtonNoPadding onClick={open} type={"link"}>
          创建项目
        </ButtonNoPadding>
      </Row>
      {/* <Button onClick={retry}>retry</Button> */}
      <SearchPanel users={users || []} param={param} setParam={setParam} />
      <ErrrorBox error={error} />
      <List loading={isLoading} users={users || []} dataSource={list || []} />
    </Container>
  );
};

ProjectListScreen.whyDidYouRender = true;

const Container = styled.div`
  padding: 3.2rem;
  width: 100%;
`;
